.container {
    padding: 20px;
    background-color: #f0f0f0;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    text-align: center;
}

.textInput {
    max-width: 800px;
    width: 100%;
    height: 30%;
    padding: 10px;
    resize: none;
    box-sizing: border-box;
    margin: 20px;
    border-radius: 10px;
}
.keyInput{
    width: 100%;
    max-width: 400px;
    box-sizing: border-box;
    resize: none;
    font-size: medium;
}

button {
    margin: 20px;
    padding: 10px;
    background-color: rgb(237, 31, 145);
    color: white;
    font-size: 1.5rem;
    border: white;
    border-radius: 10px;
}

.result{
    margin: 20px;
    word-break: break-word;
}